<template>
  <modal :name="$vnode.key"  @before-open="beforeOpen" @before-close="beforeClose"  v-body-scroll-lock="modalIsOpen" width="70%" height="70%" :delay="100" :adaptive="true"  classes="flex flex-col xl:flex-row rounded-md overflow-y-scroll xl:overflow-hidden">
        <div class="px-10 lg:px-12 py-16 flex-1 flex flex-col xl:overflow-y-auto">
            <h2 class="font-robot font-extrabold  text-3xl text-left">{{modalName}}</h2>

            <p class="text-left font-medium pt-0">{{modalDescription}}</p>

            <p class="text-left pt-6" v-html="modalText"/>

            <div v-if="modalObject.tech.length" class="flex space-x-5 mt-auto">
                <img  v-for="(a,index) of modalObject.tech" :src="require(`@/assets/icons/${a}.svg`)" :key="`svg-tech-${index}-${modalName}`" class="w-12 h-auto ">
            </div>
            <div v-if="modalObject.links.length">
                <h3 class="font-robot font-extrabold text-xl text-left py-4">Links</h3>
                <div class="text-left ">
                    <a  v-for="link of modalObject.links" :key="`l-${link}-${modalName}`" :href="`${link}`">
                        <div class="relative w-max one">
                            {{link}}
                            <span class="absolute -bottom-1 left-0 w-0 transition-all h-1 bg-blue-600"></span>
                        </div>
                    </a>
                </div>
            </div>
        </div>

        <div v-if="modalObject.content[0].type === 'vid'" class="bg-gradient-to-br from-blue-900 to-gray-900 px-1 md:px-10 flex-1">
            <iframe  :src="`${modalObject.content[0].link}?dnt=1`" 
            width="100%" height="100%" frameborder="0" 
            allow="autoplay; fullscreen; picture-in-picture" allowfullscreen>
            </iframe>
        </div>
        <div v-else-if="modalObject.content[0].type === 'img'" class="bg-gradient-to-br from-blue-900 to-gray-900 px-1 md:px-10 flex-1 flex place-content-center">
            <img class="self-center h-auto" :src="require(`@/assets/${modalObject.content[0].link}`)" :key="`content-img-${modalName}`">
        </div>
  </modal>
</template>

<script>
export default {
  name: 'ExampleModal',
  props: {
      modalName: String,
      modalDescription: String,
      modalImg: String,
      modalText: String,
      modalObject: Object
  },
  data() {
        return {
            modalIsOpen: false,   
        }
    },
  methods: {
    beforeOpen () {
      console.log('Opening...')
       this.$set(this, 'modalIsOpen', true)
    },
    beforeClose () {
      console.log('Closing...')
      this.$set(this, 'modalIsOpen', false)
    }
  }
}
</script>

<style>
.one:hover span:last-child {
  width: 100%;
}
</style>
