import Vue from 'vue'
import VModal from 'vue-js-modal'
import App from './App.vue'
import "tailwindcss/tailwind.css"
import VBodyScrollLock from 'v-body-scroll-lock'

Vue.config.productionTip = false
Vue.use(VModal)
Vue.use(VBodyScrollLock)

new Vue({
  render: h => h(App),
}).$mount('#app')
